import React from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import { mydata } from '../data';

// Components
import Layout from '../components/Layout/Layout';
import Title from '../components/Title/Title';
import Paragraph from '../components/Paragraph/Paragraph';
import BigTitle from '../components/BigTitle/BigTitle';
import More from '../components/More/More';
import Content from '../components/Content/Content';
export const query = graphql`
	{
		file(relativePath: { eq: "img/farm.png" }) {
			childImageSharp {
				fluid {
					...GatsbyImageSharpFluid
				}
			}
		}
		allContentfulBlog(limit: 6) {
			edges {
				node {
					title
					slug
					img {
						fluid {
							...GatsbyContentfulFluid
						}
					}
					createdAt
				}
			}
		}
	}
`;
function ContentPage({ data }) {
	return (
		<Layout title="مطالب مفید">
			<div className="ml-5 mr-5">
				<div className="row">
					<div className="col-lg-6 col-md-6 col-sm-12 col-12">
						<Img fluid={data.file.childImageSharp.fluid} alt="farm" style={{ width: '250px' }} />
					</div>
					<div className="col-lg-6 col-md-6 col-sm-12 col-12">
						<BigTitle>سوالات متداول در مورد آفت و کنترل آن</BigTitle>
					</div>
				</div>
				<div dir="rtl" className="row">
					<div className="col-lg-8 col-md-8 col-sm-12 col-12 mt-5">
						{mydata.map((each) => {
							return (
								<div key={each.title} className="pt-2 pb-2">
									<Title key={each.title}>{each.title}</Title>
									<Paragraph>{each.desc}</Paragraph>
								</div>
							);
						})}
					</div>
					<div className="col-lg-4 col-md-4 col-sm-12 col-12 mt-5">
						<p style={{ color: 'grey' }} className="text-right m-0 mt-3 mr-3">
							مطالب دیکر
						</p>
						<hr />
						<Content content={data.allContentfulBlog.edges} />
					</div>
				</div>
				<div className="pb-5 pt-5">
					<More />
				</div>
			</div>
		</Layout>
	);
}

export default ContentPage;
