import React from 'react';
import check from '../../img/check.png';
import call from '../../img/call.png';
import free from '../../img/free.png';
import classes from './More.module.scss';
function More() {
	const num = '%۱۰۰';
	return (
		<div className="d-flex align-items-center justify-content-center mr-3 ml-3">
			<div className="row">
				<div className="col-lg-4 col-md-4 col-sm-12 col-12">
					<div className={classes.each}>
						<div>
							<img alt="check" src={check} className="img-fluid" />
						</div>
						<h5 className="mt-3 text-center w-100" dir="rtl">
							{num} ضمانت اصل بودن کالا
						</h5>
					</div>
				</div>
				<div className="col-lg-4 col-md-4 col-sm-12 col-12">
					<div className={classes.each}>
						<div>
							<img alt="check" src={free} className="img-fluid" />
						</div>
						<h5 className="mt-3 text-center w-100" dir="rtl">
							ارسال کاملا رایگان
						</h5>
					</div>
				</div>
				<div className="col-lg-4 col-md-4 col-sm-12 col-12">
					<div className={classes.each}>
						<div>
							<img alt="check" src={call} className="img-fluid" />
						</div>
						<h5 className="mt-3 text-center w-100" dir="rtl">
							مشاوره رایگان بگیرید
						</h5>
					</div>
				</div>
			</div>
		</div>
	);
}

export default More;
