import React, { Fragment } from 'react';
import Img from 'gatsby-image';
import { Link } from 'gatsby';
import classes from './Content.module.scss';
import HR from '../HR/HR';
function Content({ content }) {
	return (
		<Fragment>
			{content.map((each) => {
				return (
					<Link key={each.node.slug} className={classes.itemlink} to={`/${each.node.slug}`}>
						<div className={classes.item}>
							<Img
								className="img-fluid"
								style={{ width: '140px' }}
								alt="back"
								fluid={each.node.img.fluid}
							/>
							<h4 dir="rtl" className="text-center m-0 pt-3">
								{each.node.title}
							</h4>
						</div>
						<HR />
					</Link>
				);
			})}
			<div className="d-flex justify-content-start mr-5">
				<Link to="/pests">مشاهده همه ی آفت ها</Link>
			</div>
		</Fragment>
	);
}

export default Content;
